.button {
    border-radius: 50%;
    background: #5acced;
    box-shadow: 20px 20px 60px #3b879c, -20px -20px 60px #79ffff;
            width: 14rem;
            height: 14rem;
            cursor: pointer;
            -webkit-animation: buttonAnimation 4s linear infinite;
            animation: buttonAnimation 4s linear infinite;
            transition: all .5s;
        }
        
.button:active {
    height: 15rem;
    width: 15rem;
    background: whitesmoke;
}


@-webkit-keyframes buttonAnimation {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(359deg);
    }
  }


@keyframes buttonAnimation {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(359deg);
    }
  }
